translations['de']['encryptionModalFirstText'] = "Wir alle haben diese Informationen, diese sehr privaten Dateien, die wir mit niemandem teilen mchten. Arbeitsbezogene oder persnliche Dokumente, Bilder und Videos - alles ist wichtig und wir verdienen digitale Privatsphre! Aus diesem Grund haben wir eine Funktion entwickelt, die diesen ultimativen Datenschutz ermglicht."
translations['de']['encryptionModalHeaderHowItWorks'] = "Wie Filen funktioniert"
translations['de']['encryptionModalHowItWorksText'] = "Wie Sie vielleicht bereits wissen, ist Filen die sicherste Methode zum Speichern von Dateien. Aber wie geht das? Nun, es ist ganz einfach. Wenn Sie eine Datei in den Cloud-Speicher von Filen hochladen, laden Sie tatschlich keine Datei hoch, sondern Teile verschlsselten Textes hoch. Bevor Sie einen Upload starten, zerlegt Ihr Browser Ihre Datei in 1 MB groe Teile, verschlsselt sie mit AES-Verschlsselung und sendet die verschlsselte Zeichenfolge an unsere Server. Auf diese Weise verlsst die eigentliche Datei niemals Ihren Computer. Sogar Dateinamen und Metadaten werden verschlsselt. Wenn Sie eine Datei herunterladen, fordert Ihr Browser alle Teile der Datei von den Servern von Filen an, entschlsselt sie mit dem Entschlsselungsschlssel und erstellt dann die gesamte Datei erneut. Auf diese Weise knnen Sie sicher sein, dass Ihre Datei immer sicher und fr niemanden lesbar ist, es sei denn, er verfgt ber den Entschlsselungsschlssel. Nicht einmal wir wissen, was Sie hochgeladen haben."
