translations['da']['encryptionModalHeaderHowItWorks'] = "Hvordan Filen viker"
translations['da']['encryptionModalHowItWorksText'] = "Som du mske allerede ved, er Filen den sikreste mde at gemme dine filer p. Men hvordan fungerer det? Det er faktisk ret simpelt. Nr du uploader en fil til Filen, uploader du faktisk ikke en fil, du uploader klumper af krypteret tekst. Fr du starter en upload, klipper din browser din fil i 1 MB stykker, krypterer dem ved hjlp af AES-kryptering og sender derefter den krypterede streng til vores servere. P den mde forlader den faktiske fil aldrig din computer. Selv filnavne og metadata er krypteret. Nr du downloader en fil, beder din browser om alle filstykker fra Filens servere, dekrypterer dem ved hjlp af dekrypteringsnglen og genopbygger derefter hele filen igen. P denne mde kan du vre sikker p, at din fil altid er sikker og ikke kan lses af nogen, medmindre de har dekrypteringsnglen. Ikke engang VI ved, hvad du har uploadet."
