translations['es']['encryptionModalHeaderHowItWorks'] = "Cmo funciona Filen"
translations['es']['encryptionModalHowItWorksText'] = "Como ya sabr, Filen es la forma ms segura de almacenar archivos. Pero como funciona? Bueno, es bastante simple. Cuando carga un archivo en el almacenamiento en la nube de Filen, en realidad no carga un archivo, carga fragmentos de texto cifrado. Antes de iniciar una carga, su navegador fragmenta su archivo en trozos de 1 MB, los cifra mediante el cifrado AES y luego enva la cadena cifrada a nuestros servidores. De esa manera, el archivo real nunca sale de su computadora. Incluso los nombres de archivo y los metadatos estn cifrados. Al descargar un archivo, su navegador solicita todos los fragmentos del archivo de los servidores de Filen, los descifra utilizando la clave de descifrado y luego reconstruye el archivo completo nuevamente. De esta forma, puede estar seguro de que su archivo siempre estar seguro y nadie podr leerlo a menos que tenga la clave de descifrado. Ni siquiera sabemos lo que has subido."
