translations['fr']['encryptionModalHeaderHowItWorks'] = "Comment Filen fonctionne"
translations['fr']['encryptionModalHowItWorksText'] = "Comme vous le savez peut-tre dj, Filen est le moyen le plus sr de stocker des fichiers. Mais comment cela fonctionne-t-il ? Eh bien, c'est trs simple. Lorsque vous importez un fichier sur le cloud de Filen, vous ne tlchargez pas vraiment un fichier, mais des morceaux de texte chiffr. Avant de lancer le tlchargement, votre navigateur dcoupe votre fichier en morceaux de 1 Mo, les crypte  l'aide du cryptage AES, puis envoie la chane crypte  nos serveurs. Ainsi, le fichier rel ne quitte jamais votre ordinateur. Mme les noms de fichiers et les mtadonnes sont crypts. Lorsque vous tlchargez un fichier, votre navigateur demande tous les morceaux du fichier aux serveurs de Filen, les dcrypte  l'aide de la cl de dcryptage, puis reconstruit l'ensemble du fichier. Ainsi, vous tes sr que votre fichier est toujours scuris et que personne ne peut le lire  moins de disposer de la cl de dcryptage. Mme nous ne savons pas ce que vous avez tlcharg."
